<template>
    <div class="code">
        <headerBar :title="headerNavData"></headerBar>
        <div class="code_center">
          <!-- <img src="../assets/images/dwn.png"> -->
          <div id="qrcode"></div>
          <p class="yqm">{{$t('data.Invitationcode')}} {{codenum}}</p>
          <p class="sao">{{$t('data.Scaninvitefriends')}}</p>
          <p class="yqm"></p>
          <!-- <button type="button"
            v-clipboard:copy="message"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError">复制地址</button> -->
            <button type="button" @click="vueCopy()">{{$t('data.copyaddress')}}</button>
        </div>
    </div>
  
</template>

<script>
  import QRCode from 'qrcodejs2'
  import headerBar from './header.vue'
  import {getCookie,setCookie} from '../assets/js/cookie.js'
  import {AndroidCopytent} from '@/api/android'
  export default { 
    components:{
      "headerBar":headerBar
    },
    data () {
      return {
        message:'',
        codenum:'',
        headerNavData:{
          left:true,
          title:this.$t('mycenter.Invitation'),
          right:false,
          bj:'#2c94ed',
          search:false,
          share:false
        }
      }
    },
    created(){
        this.code()
        this.isdwonload()
    },
    methods:{
      vueCopy(){
        var that = this
        AndroidCopytent(this.message,function(isbuloon){
          if(isbuloon){
            that.$dialog.toast({
                mes: that.$t('data.copycontent'),
                timeout: 2000
            });
          }else{
            that.$dialog.toast({
                mes: that.$t('data.copycontentfail'),
                timeout: 2000
            });
          }
        })
      },
      //查询软件更新
      isdwonload(){
          var that = this
          this.axios({
            url:this.Url + 'Upgrade.ashx?a=1',
            method:'post',
            headers:{'Content-Type':'application/x-www-form-urlencoded'}
          }).then(function (res) {
              if (res.data.success) {
                  that.message = res.data.data.downLoadUrl
                  that.qrcode()
              }
          })
      },
      qrcode () {  
        var that = this
          let qrcode = new QRCode('qrcode', {  
              width: 200,  
              height: 200, 
              text: that.message 
          })
      }, 
      // 获取邀请码
      code(){
        var that = this;
        this.axios({
          url:this.Url + 'Wallet.ashx',
          method:'post',
          params: {req:4,publickey:getCookie("publickey"),pageIndex:1,pagecount:10}
        }).then(function (res) {
            var _list = res.data.data;
            that.codenum = _list[0].invitation
        });
      },
      onCopy: function (e) {
        this.$dialog.toast({
            mes: that.$t('data.copycontent'),
            timeout: 2000
        });
      },
      onError: function (e) {
        this.$dialog.toast({
            mes: that.$t('data.copycontentfail'),
            timeout: 2000
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.code{
  padding-top: 1.65rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.code_top{
  padding: 2.1rem 0 .35rem;
  background: url('../assets/images/codebj.png') no-repeat top left;
  background-size: 100%;
  text-align: center;
}
.code_top img{
  width: .85rem;
  height: .85rem;
  border-radius: 50%;
  margin-bottom: .35rem
}
.code_top p{
  width: 80%;
  margin: 0 auto;
  white-space: wrap;
  font-size: .26rem;
  color: #fff;
  letter-spacing: 1px
}
.code_center{
  text-align: center;
  width: 80%;
  margin: 0 auto;
  background: #fff;
  box-shadow: 0 5px 16px #d7d7d7;
  padding-bottom: .5rem;
}
.code_center img{
  width: 4rem;
  height: 4rem;
  margin: .5rem auto .35rem;
}
.code_center #qrcode{
  padding-top: .5rem;
  width: 200px;
  /*height: 200px;*/
  margin: .5rem auto .35rem;
}
.code_center button{
  width: 4rem;
  color: #fff;
  font-size: .3rem;
  padding: .3rem 0;
  outline: none;
  border:none;
  background-color: #278eed;
  border-radius: 5px;
}
.code_center button:active{
  background-color: #337cc0;
}
.sao{
  font-size: .24rem;
  color: #888;
  text-align: center;
}
.yqm{
  font-size: .3rem;
  color: #333;
  text-align: center;
  margin: .2rem 0;
}
</style>



